<script>
export default {
  data() {
    return {
      changeClass: false,
      show: false,
      links: [
        { title: 'Головна', url: '/', exact: true },
        { title: 'Гурт', url: '/about' },
        { title: 'Галерея', url: '/gallery' },
        { title: 'Контакти', url: '/contacts' }
      ]
    }
  },
  methods: {
    navAnimation: function() {
      if (window.scrollY > 50) {
        this.changeClass = true;
      }
      if (window.scrollY === 0) {
        this.changeClass = false;
      }
    },
    onSwipeLeft: function() {
      this.show = true;
    },
    onSwipeRight: function() {
      this.show = false;
    }
  }
}
